import React from 'react';
import { Box, Paper, Typography, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, LinearProgress, Chip } from '@mui/material';
import DownloadIcon from '@mui/icons-material/Download';

const qrStats = {
  todaySubmit: 67,
  total: 83,
  rate: 81,
};

const notSubmitted = [
  { id: 1, user: '', meal: '', time: '18:00', count: 3, status: '' },
  { id: 2, user: '', meal: '', time: '12:00', count: 1, status: '' },
];

export default function QRPage() {
  return (
    <Box sx={{ p: 4, background: '#f2f4f6', minHeight: '100vh' }}>
      {/*   +  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Typography variant="h5" fontWeight={700}>QR  </Typography>
        <Box sx={{ display: 'flex', gap: 1 }}>
          <Button variant="outlined" startIcon={<DownloadIcon />} sx={{ fontWeight: 600, height: 40 }}>QR  </Button>
        </Box>
      </Box>
      {/*   */}
      <Box sx={{ display: 'flex', gap: 3, mb: 4 }}>
        <Paper elevation={2} sx={{ flex: 1, minWidth: 260, p: 3, borderRadius: 2, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'flex-start' }}>
          <Typography variant="body2" color="text.secondary"> QR </Typography>
          <Typography variant="h4" fontWeight={700} sx={{ mt: 1 }}>{qrStats.todaySubmit}</Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mt: 0.5 }}> {qrStats.total} </Typography>
        </Paper>
        <Paper elevation={2} sx={{ flex: 1, minWidth: 260, p: 3, borderRadius: 2, display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'flex-start' }}>
          <Typography variant="body2" color="text.secondary"></Typography>
          <Box sx={{ display: 'flex', alignItems: 'center', width: '100%', mt: 1 }}>
            <Typography variant="h4" fontWeight={700} sx={{ mr: 2 }}>{qrStats.rate}%</Typography>
            <LinearProgress variant="determinate" value={qrStats.rate} sx={{ flex: 1, height: 10, borderRadius: 5, background: '#f0f0f0', '& .MuiLinearProgress-bar': { background: 'var(--blue700)' } }} />
          </Box>
        </Paper>
      </Box>
      {/*   */}
      <Paper elevation={2} sx={{ p: 3, borderRadius: 2 }}>
        <Typography fontWeight={700} sx={{ mb: 1 }}>QR  </Typography>
        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>   QR     </Typography>
        <TableContainer>
          <Table size="small">
            <TableHead>
              <TableRow sx={{ background: '#f4f6fa' }}>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
                <TableCell sx={{ fontWeight: 700 }}> </TableCell>
                <TableCell sx={{ fontWeight: 700 }}> </TableCell>
                <TableCell sx={{ fontWeight: 700 }}> </TableCell>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {notSubmitted.map((row) => (
                <TableRow key={row.id}>
                  <TableCell>{row.user}</TableCell>
                  <TableCell>{row.meal}</TableCell>
                  <TableCell>{row.time}</TableCell>
                  <TableCell>
                    <Chip label={`${row.count}`} sx={{ background: row.count >= 3 ? '#fff1f0' : '#f4f6fa', color: row.count >= 3 ? '#f04452' : '#222', fontWeight: 700 }} />
                  </TableCell>
                  <TableCell>
                    <Chip label={row.status} sx={{ background: row.status === '' ? '#fff1f0' : '#f4f6fa', color: row.status === '' ? '#f04452' : '#10b981', fontWeight: 700 }} />
                  </TableCell>
                  <TableCell>
                    <Button variant="outlined" size="small" sx={{ fontWeight: 600, minWidth: 64, mr: 1 }}> </Button>
                    {row.status === '' && (
                      <Button variant="outlined" size="small" color="error" sx={{ fontWeight: 600, minWidth: 48 }}></Button>
                    )}
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
    </Box>
  );
} 