import React, { useState } from 'react';
import { Box, Paper, Typography, Button, TextField, Stack, MenuItem, Chip, Checkbox } from '@mui/material';

const initialEndpoints = [
  { name: '', method: 'POST', path: '/auth/login', status: 'success' },
  { name: ' ', method: 'GET', path: '/users', status: 'success' },
  { name: ' ', method: 'GET/POST', path: '/reservations', status: 'success' },
  { name: '', method: 'GET/POST', path: '/notices', status: 'fail' },
  { name: ' ', method: 'GET/POST', path: '/menus', status: 'success' },
];

const customTabs = [
  { label: ' ', value: 0 },
  { label: 'API ', value: 1 },
  { label: ' ', value: 2 },
];

export default function SettingsPage() {
  const [tab, setTab] = useState(0);
  const [siteName, setSiteName] = useState('');
  const [adminEmail, setAdminEmail] = useState('admin@example.com');
  //   
  const [endpoints, setEndpoints] = useState(initialEndpoints);
  const [editing, setEditing] = useState({}); // { idx: path }

  //  path   
  const handleEndpointChange = (idx, value) => {
    setEditing((prev) => ({ ...prev, [idx]: value }));
  };
  //   
  const handleEndpointSave = (idx) => {
    setEndpoints((prev) => prev.map((ep, i) => i === idx ? { ...ep, path: editing[idx] ?? ep.path } : ep));
    setEditing((prev) => {
      const copy = { ...prev };
      delete copy[idx];
      return copy;
    });
  };

  return (
    <Box sx={{ p: 4, background: '#f2f4f6', minHeight: '100vh' }}>
      {/*   */}
      <Typography variant="h5" fontWeight={700} sx={{ mb: 4 }}> </Typography>
      {/*  */}
      <Paper elevation={0} sx={{ mb: 0, background: 'transparent', boxShadow: 'none', mt: 1 }}>
        <Stack direction="row" spacing={2} alignItems="center" justifyContent="center" sx={{ width: '100%' }}>
          {customTabs.map(tabItem => (
            <Button
              key={tabItem.value}
              onClick={() => setTab(tabItem.value)}
              disableRipple
              sx={{
                flex: 1,
                height: 44,
                borderRadius: 1.2,
                fontWeight: tab === tabItem.value ? 700 : 500,
                fontSize: 16,
                color: tab === tabItem.value ? '#222' : '#444',
                background: tab === tabItem.value ? '#fff' : 'transparent',
                border: tab === tabItem.value ? '2px solid #222' : 'none',
                boxShadow: tab === tabItem.value ? '0 1px 6px rgba(0,0,0,0.04)' : 'none',
                transition: 'all 0.15s',
              }}
            >
              {tabItem.label}
            </Button>
          ))}
        </Stack>
      </Paper>
      {/*    */}
      <Box sx={{ mb: 3 }} />
      {/*    */}
      {tab === 0 && (
        <Paper elevation={2} sx={{ mt: 2, p: 4, borderRadius: 2, width: '100%', mx: 'auto' }}>
          <Typography fontWeight={700} sx={{ mb: 1 }}> </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>     .</Typography>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, width: '100%' }}>
            <TextField label=" " size="small" value={siteName} onChange={e => setSiteName(e.target.value)} fullWidth />
            <TextField label=" " size="small" value={adminEmail} onChange={e => setAdminEmail(e.target.value)} fullWidth />
            <Button variant="contained" sx={{ width: 100, mt: 2, fontWeight: 600, background: 'var(--blue700)' }}></Button>
          </Box>
        </Paper>
      )}
      {/* API   */}
      {tab === 1 && (
        <Box sx={{ width: '100%' }}>
          {/* API   */}
          <Paper elevation={2} sx={{ mb: 3, p: 3, borderRadius: 2, background: '#fffbe6', border: '1px solid #ffe58f' }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
              <Typography fontWeight={700}>API  </Typography>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Box sx={{ width: 8, height: 8, borderRadius: '50%', background: '#f04452', mr: 1 }} />
                <Typography color="#f04452" fontSize={14}> </Typography>
              </Box>
            </Box>
            <Typography color="#faad14" fontSize={15} sx={{ mb: 2 }}>
               API   .<br />
                ,    .<br />
                   .
            </Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography fontSize={15}> :</Typography>
              <TextField select size="small" value="5" sx={{ width: 100, background: '#fff' }}>
                <MenuItem value="1">1</MenuItem>
                <MenuItem value="5">5</MenuItem>
                <MenuItem value="10">10</MenuItem>
              </TextField>
            </Box>
          </Paper>

          {/* API  URL  */}
          <Paper elevation={2} sx={{ mb: 3, p: 3, borderRadius: 2 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 2 }}>
              <Box>
                <Typography fontWeight={700}>API  URL </Typography>
                <Typography variant="body2" color="text.secondary">SaaS   API   .</Typography>
              </Box>
              <Button variant="outlined" sx={{ fontWeight: 600, minWidth: 100 }}> </Button>
            </Box>
            <TextField size="small" fullWidth value="http://api.example.com" sx={{ mt: 1 }} />
          </Paper>

          {/* API   */}
          <Paper elevation={2} sx={{ mb: 3, p: 3, borderRadius: 2 }}>
            <Typography fontWeight={700} sx={{ mb: 2 }}>API  </Typography>
            <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
              {endpoints.map((ep, idx) => (
                <Box key={idx} sx={{ display: 'flex', alignItems: 'center', gap: 2, p: 2, border: '1px solid #f0f0f0', borderRadius: 1, background: '#fafbfc' }}>
                  <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, minWidth: 120 }}>
                    <Box sx={{ width: 10, height: 10, borderRadius: '50%', background: ep.status === 'success' ? '#10b981' : '#f04452' }} />
                    <Typography fontWeight={700} fontSize={15}>{ep.name}</Typography>
                  </Box>
                  <Chip label={ep.method} size="small" sx={{ background: '#f4f6fa', color: '#3182f6', fontWeight: 700, fontSize: 13 }} />
                  <TextField
                    size="small"
                    value={editing[idx] !== undefined ? editing[idx] : ep.path}
                    onChange={e => handleEndpointChange(idx, e.target.value)}
                    sx={{ flex: 1, minWidth: 120 }}
                  />
                  <Button variant="outlined" size="small" sx={{ fontWeight: 600, minWidth: 64, mr: 1 }}></Button>
                  <Button variant="outlined" size="small" sx={{ fontWeight: 600, minWidth: 48 }} onClick={() => handleEndpointSave(idx)}></Button>
                </Box>
              ))}
            </Box>
          </Paper>

          {/*   */}
          <Paper elevation={2} sx={{ p: 3, borderRadius: 2 }}>
            <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'flex-end', flexWrap: 'wrap' }}>
              <Box>
                <Typography fontWeight={700} sx={{ mb: 1 }}> </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>API     .</Typography>
                <Box sx={{ display: 'flex', gap: 2, mb: 2 }}>
                  <TextField label=" " size="small" value="v1.0.0" sx={{ width: 120 }} />
                  <TextField label="" size="small" value="5" sx={{ width: 120 }} />
                  <TextField label="  " size="small" value="1" sx={{ width: 120 }} />
                </Box>
                <Button variant="contained" sx={{ width: 100, fontWeight: 600, background: 'var(--blue700)' }}></Button>
              </Box>
              <Box sx={{ textAlign: 'right', minWidth: 180 }}>
                <Typography variant="body2" color="text.secondary"> </Typography>
                <Typography fontWeight={600}>2024. 5. 29</Typography>
              </Box>
            </Box>
          </Paper>
        </Box>
      )}
      {/*    */}
      {tab === 2 && (
        <Paper elevation={2} sx={{ mt: 2, p: 4, borderRadius: 2, width: '100%', mx: 'auto' }}>
          <Typography fontWeight={700} sx={{ mb: 1 }}> </Typography>
          <Typography variant="body2" color="text.secondary" sx={{ mb: 3 }}>    .</Typography>
          <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2, width: '100%' }}>
            {/*    */}
            <TextField
              select
              label="  "
              size="small"
              value={"30"}
              sx={{ width: 300 }}
            >
              <MenuItem value="30">30</MenuItem>
              <MenuItem value="60">60</MenuItem>
              <MenuItem value="90">90</MenuItem>
            </TextField>
            {/* IP  */}
            <TextField
              label="IP  "
              size="small"
              placeholder="IP   ( )"
              sx={{ width: '100%' }}
              helperText={"  IP  . (: 127.0.0.1, 192.168.0.1)"}
            />
            {/* 2  */}
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
              <Typography>2  </Typography>
              <Checkbox sx={{ ml: 1 }} />
              <Typography sx={{ ml: 1 }}></Typography>
            </Box>
            <Button variant="contained" sx={{ width: 100, mt: 2, fontWeight: 600, background: 'var(--blue700)' }}></Button>
          </Box>
        </Paper>
      )}
    </Box>
  );
} 