import React, { useState } from 'react';
import { Box, Paper, Typography, Button, Table, TableBody, TableCell, TableContainer, TableHead, TableRow, TextField, Select, MenuItem, Chip, IconButton } from '@mui/material';
import AddIcon from '@mui/icons-material/Add';
import DownloadIcon from '@mui/icons-material/Download';
import EditOutlinedIcon from '@mui/icons-material/EditOutlined';
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import DeleteOutlineOutlinedIcon from '@mui/icons-material/DeleteOutlineOutlined';

const statCards = [
  { label: ' ', value: 4 },
  { label: ' ', value: 2 },
  { label: ' ', value: 2 },
  { label: ' ', value: 876 },
];

const posts = [
  { id: 1, title: '1   !  ', isNew: true, fixed: true, category: '', author: '', status: '', views: 342, date: '2024-01-15', content: '    1     .       .' },
  { id: 2, title: '    ', isNew: false, fixed: false, category: '', author: '', status: '', views: 156, date: '2024-01-10', content: '       .  ,      .' },
  { id: 3, title: '   ', isNew: false, fixed: true, category: '', author: '', status: '', views: 289, date: '2024-01-08', content: '      .        .' },
  { id: 4, title: '2    ', isNew: false, fixed: false, category: '', author: '', status: '', views: 89, date: '2024-01-05', content: '2    .' },
];

export default function SupportPage() {
  const [search, setSearch] = useState('');
  const [catFilter, setCatFilter] = useState('');
  const [statusFilter, setStatusFilter] = useState('');

  return (
    <Box sx={{ p: 4, background: '#f2f4f6', minHeight: '100vh' }}>
      {/*   +  */}
      <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
        <Typography variant="h5" fontWeight={700}>  </Typography>
        <Box sx={{ display: 'flex', gap: 1 }}>
          <Button variant="outlined" startIcon={<DownloadIcon />} sx={{ fontWeight: 600, height: 40 }}> </Button>
          <Button variant="contained" startIcon={<AddIcon />} sx={{ fontWeight: 600, height: 40, background: 'var(--blue700)' }}>+   </Button>
        </Box>
      </Box>
      {/*   */}
      <Box sx={{ display: 'flex', gap: 3, mb: 4 }}>
        {statCards.map((card, idx) => (
          <Paper key={card.label} elevation={2} sx={{ flex: 1, minWidth: 180, p: 2.5, textAlign: 'center', borderRadius: 2, boxShadow: '0 2px 8px rgba(0,0,0,0.06)', display: 'flex', flexDirection: 'column', justifyContent: 'center', alignItems: 'center' }}>
            <Typography variant="body2" color="text.secondary">{card.label}</Typography>
            <Typography variant="h5" fontWeight={700} sx={{ mt: 1 }}>{card.value}</Typography>
          </Paper>
        ))}
      </Box>
      {/*   */}
      <Paper elevation={2} sx={{ p: 3, borderRadius: 2, mb: 4 }}>
        <Typography fontWeight={700} sx={{ mb: 1 }}>  </Typography>
        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>        .</Typography>
        <Box sx={{ display: 'flex', gap: 1, alignItems: 'center', mb: 2 }}>
          <TextField size="small" placeholder=" ..." value={search} onChange={e => setSearch(e.target.value)} sx={{ width: 220, background: '#f9fafb' }} />
          <Select size="small" value={catFilter} onChange={e => setCatFilter(e.target.value)} sx={{ width: 100, background: '#f9fafb' }}>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
          </Select>
          <Select size="small" value={statusFilter} onChange={e => setStatusFilter(e.target.value)} sx={{ width: 100, background: '#f9fafb' }}>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
            <MenuItem value=""></MenuItem>
          </Select>
        </Box>
        <TableContainer>
          <Table size="small">
            <TableHead>
              <TableRow sx={{ background: '#f4f6fa' }}>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
                <TableCell sx={{ fontWeight: 700 }}></TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {posts.filter(p => (catFilter === '' || p.category === catFilter) && (statusFilter === '' || p.status === statusFilter) && (!search || p.title.includes(search))).map((row) => (
                <TableRow key={row.id}>
                  <TableCell>
                    <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      {row.title}
                      {row.isNew && <Chip label="NEW" size="small" sx={{ background: '#f04452', color: '#fff', fontWeight: 700, fontSize: 12, ml: 1 }} />}
                      {row.fixed && <Chip label="" size="small" sx={{ background: '#f4f6fa', color: '#222', fontWeight: 700, fontSize: 12, ml: 1 }} />}
                    </Box>
                  </TableCell>
                  <TableCell>
                    <Chip label={row.category} size="small" sx={{ background: row.category === '' ? '#e6f9f2' : row.category === '' ? '#e3f2fd' : '#f4f6fa', color: row.category === '' ? '#10b981' : row.category === '' ? '#3182f6' : '#888', fontWeight: 700, fontSize: 13 }} />
                  </TableCell>
                  <TableCell>{row.author}</TableCell>
                  <TableCell>
                    <Chip label={row.status} size="small" sx={{ background: '#e6f9f2', color: '#10b981', fontWeight: 700, fontSize: 13 }} />
                  </TableCell>
                  <TableCell>{row.views}</TableCell>
                  <TableCell>{row.date}</TableCell>
                  <TableCell>
                    <IconButton size="small"><VisibilityOutlinedIcon /></IconButton>
                    <IconButton size="small"><EditOutlinedIcon /></IconButton>
                    <IconButton size="small"><DeleteOutlineOutlinedIcon /></IconButton>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Paper>
      {/*    */}
      <Paper elevation={2} sx={{ p: 3, borderRadius: 2 }}>
        <Typography fontWeight={700} sx={{ mb: 1 }}>  </Typography>
        <Typography variant="body2" color="text.secondary" sx={{ mb: 2 }}>       .</Typography>
        {posts.slice(0, 3).map((row) => (
          <Paper key={row.id} elevation={0} sx={{ mb: 2, p: 2, borderRadius: 2, background: '#f8fafc', border: '1px solid #f0f0f0' }}>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1 }}>
              <Typography fontWeight={700}>{row.title}</Typography>
              {row.isNew && <Chip label="NEW" size="small" sx={{ background: '#f04452', color: '#fff', fontWeight: 700, fontSize: 12, ml: 1 }} />}
              {row.fixed && <Chip label="" size="small" sx={{ background: '#f4f6fa', color: '#222', fontWeight: 700, fontSize: 12, ml: 1 }} />}
            </Box>
            <Typography variant="body2" color="text.secondary" sx={{ mb: 1 }}>{row.content}</Typography>
            <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
              <Typography variant="caption" color="text.secondary">{row.category} | {row.author} |  {row.views} | {row.date}</Typography>
              <Box sx={{ flex: 1 }} />
              <IconButton size="small"><VisibilityOutlinedIcon /></IconButton>
              <IconButton size="small"><EditOutlinedIcon /></IconButton>
            </Box>
          </Paper>
        ))}
      </Paper>
    </Box>
  );
} 